.calender_weekday_wrap {
	.period_header {
		padding-bottom: 30px;
		text-align: center;
	}

	.btn_prev {
		svg {
			transform: rotate(90deg);
		}
	}
	.btn_next {
		svg {
			transform: rotate(-90deg);
		}
	}

	.period {
		padding: 0 12px;
		font-size: 18px;
		line-height: 22px;
	}
}

#wrap {
	.calendar_default_main {
		margin-top: 35px;
		border: 0;

		& > div:nth-child(2) {
			height: 500px !important;
		}

		tr {
			tr  {
				&:nth-child(odd) {
					.calendar_default_cell_inner {
						border-bottom: 0;
					}
				}
			}
		}

		td {
			& > div {
				width: 100%;
			}
		}

		[class*='_inner'] {
			//border-width: 0 0 1px 0;
			background-color: transparent;
			border-color: #E1E1E1;
		}

		.calendar_default_rowheader_inner {
			border-width: 0 1px 0 0;
			padding: 0 10px 0 0;
			font-size: 10px;
			line-height: 12px;
			text-align: left;
			color: #666;

			& > div {
				margin-top: -2px;
			}

			.calendar_default_rowheader_minutes{
				font-size: 10px;
				vertical-align: top;
			}
		}

		.calendar_default_colheader_inner {
			flex-direction: column;
			justify-content: start;
			border-width: 0 0 1px 0;
			text-align: center;

			.day {
				font-size: 10px;
				line-height: 12px;

				&.today {
					color: #25AAF5;
				}
			}

			.num {
				display: inline-block;
				margin-top: 8px;
				width: 32px;
				height: 32px;
				color: #333;
				font-size: 20px;
				font-weight: 400;
				line-height: 32px;
				vertical-align: top;

				&.today {
					border-radius: 50%;
					background-color: #25AAF5;
					color: #fff;
				}
			}
		}

		.calendar_default_corner_inner {
			border: 0;
		}

		.calendar_default_cell_inner {
			border-width: 0 1px 1px 0;
		}


		.calendar_default_event_bar {
			display: none;
		}

		.calendar_default_event {
			//pointer-events: none;

		}

		.calendar_default_event_inner {
			display: flex;
			padding: 2px 4px;
			border: 0;
			background: #5784FC;
			font-size: 10px;
			line-height: 12px;
			color: #fff;
			white-space: normal;
			align-items: center;
			border-radius: 2px;
		}
	}
}
